.d-print-none {
  display: none !important;
}

a[data-href] {
  display: inline-block;
  text-indent: -200vw;
  visibility: hidden;

  &::before {
    content: attr(data-href);
    float: left;
    visibility: visible;
    font-weight: 600;
    text-indent: 0;
  }
}

body {
  font-family: sans-serif;
}

a {
  color: inherit !important;
}

@page  {
  margin: 20mm;
}

html,
body {
  margin: 0;
  padding: 0;
}

html {
  font-size: 14pt;
}

body {
  color: #000;
}

.lead {
  font-size: 1.1rem;
  font-weight: normal;
}

main {
  padding: 0 !important;
  margin: 0 !important;
  max-width: 100% !important;
  width: auto !important;
}

header.main {
  padding: 0 0 0.5rem 0;
  margin: 0 0 1rem 0;
  border-bottom: 3pt solid #ccc;
  position: initial !important;

  h1 {
    margin-left: 0;
    padding-left: 0;
    transform: none !important;
    font-size: 4rem !important;

    &::before {
      content: none;
    }
  }

  .text-muted {
    color: inherit !important;
  }

  aside p {
    font-weight: bolder;
  }

  ul.links.row {
    flex-direction: column;
    align-items: flex-end;

    > li {
      margin-right: 0;
    }
  }
}

.sticky {
  position: initial !important;
}

section.skills {
  &.text-muted {
    color: inherit !important;
  }
}

.section {
  .title {
    font-size: 2rem;
    font-weight: normal;
  }

  > header {
    .title {
      transform: none !important;

      &:before {
        border-bottom: none !important;
      }
    }
  }
  article {
    > header h2 {
      strong {
        font-weight: bolder;
      }
    }
    .title {
      font-size: 1.5rem;
      font-weight: normal;
    }

    &.summary.summary-print {
      break-inside: avoid-page;
      margin-bottom: 0.5rem;
      font-size: 0.9rem;

      .section-inline {
        margin-bottom: 0.5rem;
      }

      header {
        h1, h2 {
          font-size: 1rem;
          font-weight: normal;
          display: inline;
        }

        h1 {
          font-weight: bold;
        }

        h2 {
          &:before {
            content: " â€” "
          }

          strong {
            display: none;
          }
        }
      }
    }
  }
}

.section-main {
  margin-bottom: inherit;
}
